:root {
  --color-parchment: hsl(40, 35%, 88%);
  --color-parchment-dark: hsl(35, 25%, 75%);
  --color-ink-black: hsl(220, 35%, 6%);
  --color-ink-dark: hsl(225, 30%, 10%);
  --color-blood-red: hsl(355, 75%, 45%);
  --color-blood-dark: hsl(350, 70%, 30%);
  --color-royal-gold: hsl(42, 90%, 55%);
  --color-royal-gold-light: hsl(45, 85%, 65%);
  --color-royal-gold-dark: hsl(38, 80%, 40%);
  --color-mystic-purple: hsl(280, 50%, 40%);
  --color-aged-bronze: hsl(30, 40%, 35%);
  --color-midnight-blue: hsl(220, 60%, 20%);
  --color-bg-primary: var(--color-ink-black);
  --color-bg-secondary: var(--color-ink-dark);
  --color-bg-tertiary: hsl(225, 28%, 14%);
  --color-bg-elevated: hsl(220, 25%, 12%);
  --color-bg-card: linear-gradient(180deg, hsl(225, 30%, 12%) 0%, hsl(220, 35%, 8%) 100%);
  --color-accent-primary: var(--color-royal-gold);
  --color-accent-secondary: var(--color-blood-red);
  --color-accent-tertiary: var(--color-mystic-purple);
  --color-text-primary: var(--color-parchment);
  --color-text-secondary: hsl(35, 20%, 70%);
  --color-text-muted: hsl(30, 15%, 50%);
  --color-text-gold: var(--color-royal-gold);
  --color-text-blood: var(--color-blood-red);
  --color-success: hsl(145, 60%, 40%);
  --color-warning: hsl(35, 85%, 50%);
  --color-danger: var(--color-blood-red);
  --color-info: hsl(200, 70%, 50%);
  --gradient-gold: linear-gradient(
    135deg,
    var(--color-royal-gold-light) 0%,
    var(--color-royal-gold) 50%,
    var(--color-royal-gold-dark) 100%
  );
  --gradient-blood: linear-gradient(
    135deg,
    hsl(355, 70%, 55%) 0%,
    var(--color-blood-red) 50%,
    var(--color-blood-dark) 100%
  );
  --gradient-parchment: linear-gradient(180deg, var(--color-parchment) 0%, var(--color-parchment-dark) 100%);
  --gradient-dark: linear-gradient(180deg, var(--color-ink-dark) 0%, var(--color-ink-black) 100%);
  --gradient-mystic: linear-gradient(135deg, var(--color-mystic-purple) 0%, hsl(260, 45%, 25%) 100%);
  --gradient-border-gold: linear-gradient(
    90deg,
    transparent 0%,
    var(--color-royal-gold) 20%,
    var(--color-royal-gold-light) 50%,
    var(--color-royal-gold) 80%,
    transparent 100%
  );
  --gradient-border-blood: linear-gradient(
    90deg,
    transparent 0%,
    var(--color-blood-red) 30%,
    var(--color-blood-red) 70%,
    transparent 100%
  );
  --texture-noise: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.85' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)'/%3E%3C/svg%3E");
  --pattern-diamond: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 10px,
    rgba(200, 170, 100, 0.03) 10px,
    rgba(200, 170, 100, 0.03) 11px
  );
  --pattern-lines: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 20px,
    rgba(200, 170, 100, 0.02) 20px,
    rgba(200, 170, 100, 0.02) 21px
  );
  --border-subtle: 1px solid rgba(200, 170, 100, 0.15);
  --border-gold: 2px solid var(--color-royal-gold);
  --border-gold-thin: 1px solid var(--color-royal-gold);
  --border-blood: 2px solid var(--color-blood-red);
  --border-ornate: 3px double var(--color-royal-gold);
  --border-radius-sm: 4px;
  --border-radius-md: 8px;
  --border-radius-lg: 12px;
  --border-radius-xl: 20px;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.4);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.5);
  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.6);
  --shadow-gold: 0 0 20px rgba(200, 160, 60, 0.3);
  --shadow-gold-strong: 0 0 30px rgba(200, 160, 60, 0.5);
  --shadow-blood: 0 0 20px rgba(180, 50, 50, 0.3);
  --shadow-inset: inset 0 2px 4px rgba(0, 0, 0, 0.3);
  --shadow-card: 0 4px 20px rgba(0, 0, 0, 0.5), inset 0 1px 0 rgba(200, 170, 100, 0.1);
  --font-primary: 'Inter', 'Noto Sans SC', sans-serif;
  --font-display: 'Cinzel', 'Noto Serif SC', serif;
  --font-ornate: 'Cinzel Decorative', 'Noto Serif SC', serif;
  --transition-fast: 0.15s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.5s ease;
  --transition-dramatic: 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  --z-base: 1;
  --z-dropdown: 100;
  --z-modal: 1000;
  --z-tooltip: 2000;
  --z-notification: 3000;
}
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--color-ink-dark);
  border-left: 1px solid rgba(200, 170, 100, 0.2);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, var(--color-royal-gold-dark) 0%, var(--color-aged-bronze) 100%);
  border-radius: 4px;
  border: 1px solid var(--color-royal-gold);
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, var(--color-royal-gold) 0%, var(--color-royal-gold-dark) 100%);
}
::selection {
  background: var(--color-blood-red);
  color: var(--color-parchment);
}
